.beams-section {
    background: #010101;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.beams-section .tables {
    width: 100%;
    margin: calc(100vh - 356px) 0 0 0;
    transform: translateX(120%);
}
.beams-section .tables-wrapper {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    position: relative;
}
.beams-section .tables-wrapper .table-con {
    position: relative;
    flex: 0 0 calc(100% / 3);
    display: flex;
    justify-content: center;
}
.beams-section .tables-wrapper .second-con {
    flex: 0 0 calc((100% / 3) - 40px);
}
.beams-section .tables-wrapper .third-con {
    right: calc((-100% / 3) / 2);
}
.beams-section .tables-wrapper .first-con {
    right: calc((100% / 3) / 2);
}
.beams-section .second-con {
    margin: 0 20px;
    max-width: 100%;
}
.beams-section .table-1 {
    transform-origin: 50% 100%;
}